/*===== global styles =====*/
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  font-family: var(--font_main);
  color: var(--color_dark);
}

body.lock {
  overflow: hidden;
}

.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 30px;
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  font-family: var(--font_main);
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.block__title {
  font-weight: 600;
  font-size: 36px;
  line-height: 120%;
  color: var(--color_dark);
}

.btn__link {
  width: 200px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  color: var(--color_dark);
  transition: background-color 0.2s ease-in-out;
  cursor: pointer;
}

.btn__yellow {
  background: #ffcb09;
  cursor: pointer;
}
.btn__yellow:hover {
  background: #ffd741;
}

.btn__grey {
  cursor: pointer;
  background: #efefef;
}
.btn__grey:hover {
  background: #ffcb09;
}

/*===== max-width 1279px =====*/
@media (max-width: 1279px) {
  .container {
    max-width: 100%;
    padding: 0 20px;
  }

  .block__title {
    font-size: 30px;
  }
}
/*===== max-width 767px =====*/
@media (max-width: 767px) {
  .container {
    padding: 0 10px;
  }

  .block__title {
    font-size: 24px;
  }
}
* {
  box-sizing: border-box;
}

*,
p,
h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*===== header styles =====*/
.header {
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
}
.header .container {
  max-width: 1718px;
  display: flex;
  justify-content: space-between;
}

.header__top {
  padding: 9px 0 8px;
  border-bottom: 1px solid #e3e3e3;
}

.header__top .container,
.header__bottom .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__bottom {
  padding: 13px 0;
}

.header__menu-list {
  display: flex;
  align-items: center;
}
.header__menu-list > li {
  cursor: pointer;
}
.header__menu-list > li:not(:last-child) {
  margin-right: 70px;
}
.header__menu-list a {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  white-space: nowrap;
  color: #1f1e1d;
  transition: color 0.2s ease-in-out;
}
.header__menu-list a:hover {
  color: #ffcb09;
}
.header__menu-list .menu-item-has-children {
  position: relative;
}
.header__menu-list .menu-item-has-children.active > a {
  color: #ffcb09;
}
.header__menu-list .menu-item-has-children > a {
  position: relative;
  z-index: 2;
}
.header__menu-list .menu-item-has-children > a::before, .header__menu-list .menu-item-has-children > a::after {
  content: "";
  position: absolute;
  right: 0;
  top: 7px;
  width: 9px;
  height: 6px;
  transition: all 0.2s linear;
}
.header__menu-list .menu-item-has-children > a::before {
  background: url("../images/header/arrow-down.svg") center/contain no-repeat;
}
.header__menu-list .menu-item-has-children > a::after {
  opacity: 0;
  background: url("../images/header/arrow-down-yellow.svg") center/contain no-repeat;
}
.header__menu-list .menu-item-has-children::after {
  display: none;
  content: "";
  position: absolute;
  bottom: -16px;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 20px;
  cursor: default;
}
.header__menu-list .menu-item-has-children > a {
  padding-right: 17px;
}
.header__menu-list .menu-item-has-children.active::after {
  display: block;
}
.header__menu-list .menu-item-has-children.active .sub-menu {
  visibility: visible;
  opacity: 1;
}
.header__menu-list .menu-item-has-children.active > a::before, .header__menu-list .menu-item-has-children.active > a::after {
  transform: scaleY(-1);
}
.header__menu-list .menu-item-has-children.active > a::before {
  opacity: 0;
}
.header__menu-list .menu-item-has-children.active > a::after {
  opacity: 1;
}
.header__menu-list .menu-item-has-children a:hover a::before,
.header__menu-list .menu-item-has-children a:hover a::after {
  transform: scaleY(-1);
}
.header__menu-list .menu-item-has-children a:hover a::before {
  opacity: 0;
}
.header__menu-list .menu-item-has-children a:hover a::after {
  opacity: 1;
}
.header__menu-list .sub-menu {
  position: absolute;
  top: 32px;
  left: 0;
  z-index: 5;
  width: 274px;
  padding: 30px 18px 35px 18px;
  background: #fff;
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
  border-radius: 0px 0px 10px 10px;
  cursor: default;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
.header__menu-list .sub-menu li:not(:last-child) {
  margin-bottom: 20px;
}

.header__search {
  width: 559px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  width: 33%;
  height: 36px;
  background: #f5f7fb;
  border-radius: 100px;
  cursor: pointer;
}
.header__search img {
  margin: 0 14px 0 30px;
}
.header__search p {
  flex: 1;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #999;
}

.header__logo {
  position: relative;
  width: 203px;
  height: 39px;
}

.header__logo--big {
  display: block;
  width: 100%;
  height: auto;
}

.header__logo--small {
  display: none;
  width: 100%;
  height: auto;
}

.header__contacts {
  display: flex;
  align-items: center;
}

.header__item {
  position: relative;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #1f1e1d;
  min-width: max-content;
}
.header__item:nth-child(1) {
  margin-right: 49px;
}
.header__item:nth-child(2) {
  margin-right: 63px;
}
.header__item:nth-child(3) {
  margin-right: 59px;
}

.header__contact {
