/*===== max-width 1279px =====*/
@media (max-width: 1279px) {
  .header {
    position: relative;
    z-index: 10;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 13px 20px;
  }
  .header.header__fixed::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-color: #fff;
  }

  .header .container {
    max-width: 100%;
    padding: 0;
  }

  .header__wrapper {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    z-index: -1;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    padding: 50px 20px 29px;
    background-color: #fff;
    transition: all 0.2s ease-out;
  }
  .header__fixed .header__wrapper {
    transform: translateX(100%);
  }

  .header__top {
    flex-grow: 1;
    border: none;
    padding-bottom: 0;
    margin-bottom: 43px;
  }

  .header__search {
    display: none;
  }

  .header__logo {
    width: 115px;
    height: 24px;
  }

  .header__logo--big {
    display: none;
  }

  .header__logo--small {
    display: block;
  }

  .header__menu-small {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header__menu ul {
    display: block;
  }

  .header__menu-list > li:not(:last-child) {
    margin-bottom: 20px;
  }

  .header__contacts {
    gap: 0 50px;
  }

  .header__item:nth-child(n) {
    margin-right: 0;
  }
  .header__item:nth-child(1) {
    order: 1;
  }
  .header__item:nth-child(2) {
    order: 2;
  }
  .header__item:nth-child(3) {
    order: 4;
  }
  .header__item:nth-child(4) {
    order: 3;
  }

  .header__menu-list a {
    font-size: 18px;
    line-height: 100%;
    font-weight: 500;
  }
  .header__menu-list li,
.header__menu-list li:not(:last-child) {
    margin-right: 0;
  }
  .header__menu-list > li {
    cursor: default;
  }
  .header__menu-list > li:hover > a::after, .header__menu-list > li:hover > a::before {
    transform: none;
  }
  .header__menu-list .sub-menu {
    max-height: 0;
    overflow: hidden;
    position: static;
    visibility: visible;
    opacity: 1;
    padding: 0;
    box-shadow: none;
    border-radius: 0;
    transition: all 0.2s linear;
  }
  .header__menu-list .sub-menu a {
    font-size: 14px;
    font-weight: 400;
  }
  .header__menu-list .sub-menu li:first-child {
    margin-top: 16px;
  }
  .header__menu-list .menu-item-has-children > a::before, .header__menu-list .menu-item-has-children > a::after {
    top: 10px;
  }
  .header__menu-list .menu-item-has-children:hover > a::before, .header__menu-list .menu-item-has-children:hover > a::after {
    transform: none;
  }
  .header__menu-list .menu-item-has-children a:hover::before {
    opacity: 0;
  }
  .header__menu-list .menu-item-has-children a:hover::after {
    opacity: 1;
  }
  .header__menu-list .menu-item-has-children.active::after {
    display: none;
  }
  .header__menu-list .menu-item-has-children.active > a::before {
    opacity: 0;
    transform: scaleY(-1);
  }
  .header__menu-list .menu-item-has-children.active > a::after {
    opacity: 1;
    transform: scaleY(-1);
  }
}
/*===== max-width 767px =====*/
@media (max-width: 767px) {
  .header__search-field {
    width: 80vw;
