  position: relative;
  width: 68.38%;
  min-height: 300px;
  box-sizing: border-box;
  padding: 63px 65px 70px 36px;
  border: 1px solid rgba(227, 227, 227, 0.5);
  border-radius: 10px;
  transition: all 0.2s ease-in-out;
}
