}

.reviews__link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
}

.reviews__nav {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 2;
  width: 100%;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
}

.reviews__prev,
.reviews__next {
  position: relative;
  min-width: 40px;
  min-height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: none;
  appearance: none;
  background-color: transparent;
  cursor: pointer;
}
.reviews__prev img,
.reviews__next img {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  transform: translate(-50%, -50%);
}

.reviews__prev {
  left: -70px;
}

.reviews__next {
  right: -70px;
}

.reviews__navButton {
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0px 4px 15px rgba(22, 37, 55, 0.1);
  transition: all 0.2s ease-in-out;
}

.reviews__prev:hover .reviews__navButton,
.reviews__next:hover .reviews__navButton {
  transform: scale(1.1);
}

.reviews__pagination {
  display: none;
  justify-content: center;
}

.reviews__dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin-right: 5px;
  background-color: #c4c4c4;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}
.reviews__dot:hover {
  background-color: #666;
}
.reviews__dot:last-child {
  margin-right: 0;
}

.reviews__dot-active {
  background-color: #ffcb09;
}
.reviews__dot-active:hover {
  background-color: #ffcb09;
}

.reviews__fullscreen {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1500;
  background-color: rgba(153, 153, 153, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.2s ease-in-out;
  visibility: hidden;
  opacity: 0;
  cursor: pointer;
}
.reviews__fullscreen.active {
  visibility: visible;
  opacity: 1;
}

.reviews__fullscreen-wrapper {
  position: relative;
  height: 90vh;
  cursor: default;
}

.reviews__fullscreen-img {
  display: block;
  height: 100%;
  width: auto;
}

.reviews__fullscreen-closer {
  position: absolute;
  top: -25px;
  right: -25px;
  cursor: pointer;
}

