.feedback.second__feedback {
  padding: 80px 0 100px;
  background-color: #f5f7fb;
}

/*     */
.element-animation {
  opacity: 0;
  transform: translateY(20px);
}

.element-animation.element-show {
  opacity: 1;
  transition: all 1s;
  transform: translateY(0%);
}

/*      */
input::placeholder {
  transition: color 0.2s ease-in-out;
}

input:focus::placeholder {
  color: transparent;
}

form {
  position: relative;
}

.error__message {
  position: absolute;
  top: -23px;
  left: 0;
  font-size: 12px;
  line-height: 170%;
  color: #fc3a3a;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}
.error__message.show {
  visibility: visible;
  opacity: 1;
}

/*==== animations ====*/
@keyframes addItems {
  from {
    opacity: 0;
    transform: translateY(5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
