/*===== fixed styles =====*/
.fixed {
  position: fixed;
  right: 18px;
  bottom: 18px;
  z-index: 1000;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s ease-in-out;
}
.fixed.show {
  visibility: visible;
  opacity: 1;
}
@media (max-width: 1023px) {
  .fixed {
    right: 10px;
    bottom: 10px;
  }
}

.fixed__toTop {
  position: relative;
  margin-bottom: 13px;
  width: 54px;
  height: 54px;
  border: none;
  appearance: none;
  background-color: transparent;
  cursor: pointer;
}
.fixed__toTop img {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  transform: translate(-50%, -50%);
}

.button-bg {
  display: inline-block;
  width: 54px;
  height: 54px;
  background-color: #fff;
  border-radius: 50%;
