  fill: #ffd741;
}

.fixed__feedback {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1200;
  width: 670px;
  min-height: 500px;
  padding: 93px 175px 90px 175px;
  background: #fff;
  border-radius: 10px;
  transition: opacity 0.2s ease-in-out;
  visibility: visible;
  opacity: 0;
  transform: scale(0);
}
.fixed__feedback.active {
  transform: scale(1) translate(-50%, -50%);
  visibility: visible;
  opacity: 1;
