  position: relative;
  border-radius: 10px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: border-color 0.2s ease-out;
  cursor: pointer;
}
li .page-numbers.prev:hover,
li .page-numbers.next:hover {
  border-color: #ffcb09;
}
li .page-numbers.prev::after,
li .page-numbers.next::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

li .page-numbers.prev::after {
  width: 9px;
  height: 12px;
  background: url("./../images/intro/intro-prev.svg") center/contain no-repeat;
}

li .page-numbers.next::after {
