/*===== footer styles =====*/
.footer {
  padding: 43px 0 0;
}

.footer__logo {
  max-width: 252px;
  position: relative;
  margin-bottom: 30px;
}
.footer__logo a {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.footer__wrapper {
  display: grid;
  grid-template-areas: "info nav" "other nav" "social nav";
  grid-template-columns: 330px 165px;
  grid-template-rows: 180px 67px 30px;
  gap: 43px 370px;
  margin-bottom: 33px;
}

.footer__info {
  grid-area: info;
}

.footer__nav {
  grid-area: nav;
}

.footer__other {
  grid-area: other;
}

.footer__social {
  grid-area: social;
  display: flex;
  justify-content: space-between;
}

.ft_link {
  transition: color 0.2s ease-in-out;
}
.ft_link:hover {
  color: #ffcb09;
}

.footer__text {
  position: relative;
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 170%;
  color: #4d5051;
}
.footer__text:last-child {
  margin-bottom: 0;
}
.footer__text path {
  transition: fill 0.2s ease-in-out;
}
.footer__text:hover path {
  fill: #ffcb09;
}

.footer__icon {
  margin-right: 8px;
}

.footer__cLink {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.footer__link {
  display: block;
  width: 100%;
  margin-bottom: 33px;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #1f1e1d;
}

.footer__el {
  display: block;
  width: 100%;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #1f1e1d;
}
.footer__el:first-child {
  margin-bottom: 23px;
}

.footer__item {
  position: relative;
}
.footer__item a {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}
.footer__item path {
  transition: color 0.2s ease-in-out;
}
.footer__item:hover path {
  fill: #ffd741;
}

.footer__bottom {
  padding: 20px 0 21px;
  background-color: #f5f7fb;
}

.footer__bottom .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__company,
.footer__dev {
  font-size: 14px;
  line-height: 170%;
  color: #4d5051;
}
.footer__company a,
.footer__dev a {
  transition: color 0.2s ease-in-out;
}
.footer__company a:hover,
.footer__dev a:hover {
  color: #1f1e1d;
}

/*===== max-width 1279px =====*/
@media (max-width: 1279px) {
  .footer {
    padding-top: 100px;
  }

  .footer__logo {
    max-width: 302px;
  }
  .footer__logo img {
    display: block;
    width: 100%;
    height: auto;
  }

  .footer__wrapper {
    gap: 43px 220px;
  }

  .footer__company,
.footer__dev {
    font-size: 12px;
    line-height: 170%;
    color: #4d5051;
  }
}
/*===== max-width 1023px =====*/
@media (max-width: 1023px) {
  .footer {
    padding-top: 43px;
  }

  .footer__logo {
    max-width: 210px;
    margin-bottom: 22px;
  }

  .footer__wrapper {
    grid-template-areas: "info nav social" "info nav other";
    grid-template-columns: 191px 191px 210px;
    grid-template-rows: 68px 160px;
    gap: 20px 63px;
    margin-bottom: 33px;
  }

  .footer__text {
    margin-bottom: 20px;
  }

  .footer__link {
    margin-bottom: 20px;
  }

  .footer__social {
    display: grid;
    grid-template-columns: repeat(4, 29px);
    grid-template-rows: repeat(2, 29px);
    gap: 10px 15px;
    justify-content: start;
  }

  .footer__company {
    width: 41.1%;
  }

  .footer__dev {
    width: 33.5%;
  }
}
/*===== max-width 767px =====*/
@media (max-width: 767px) {
  .footer__wrapper {
    grid-template-areas: "info nav" "other social";
    grid-template-columns: 220px 191px;
    grid-template-rows: 200px 75px;
    gap: 20px 31px;
  }

  .footer__text {
    margin-bottom: 15px;
    font-size: 12px;
  }

  .footer__link {
    margin-bottom: 15px;
    font-size: 16px;
  }

  .footer__el {
    font-size: 16px;
  }
  .footer__el:first-child {
    margin-bottom: 15px;
  }

  .footer__social {
    display: grid;
    grid-template-columns: repeat(4, 29px);
    grid-template-rows: repeat(2, 29px);
    gap: 10px 15px;
    justify-content: start;
    align-self: center;
  }

  .footer__bottom .container {
    display: block;
  }

  .footer__company {
    width: 100%;
    margin-bottom: 20px;
  }

  .footer__dev {
    width: 100%;
  }
}
/*===== max-width 479px =====*/
@media (max-width: 479px) {
  .footer__logo {
    max-width: 182px;
  }

  .footer__wrapper {
    grid-template-areas: "info" "social" "other" "nav";
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }

  .footer__social {
    grid-template-columns: repeat(8, 29px);
    grid-template-rows: auto;
    gap: 10px;
  }

  .footer__el:first-child {
    margin-bottom: 20px;
  }

  .footer__link {
    margin-bottom: 20px;
  }
  .footer__link:last-child {
    margin-bottom: 0;
  }
}