.first {
  padding: 71px 0 70px;
  background-color: var(--color_light_blue);
  overflow: hidden;

  @include md {
    padding: 81px 0 120px;
  }

  @include r(991px) {
    padding: 55px 0 90px;
  }

  @include xxs {
    padding: 58px 0 100px;
  }

  @include xxxs {
    padding: 30px 0 120px;
  }
}

.first__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 0 8px;
}

.first__info {
  max-width: 540px;
  padding-top: 50px;

  @include md {
    max-width: 528px;
    padding-top: 36px;
  }

  @include r(991px) {
    max-width: 88.88%;
  }

  @include xs {
    max-width: 100%;
  }

  @include xxxs {
    padding-top: 0;
  }
}

.first__breadcrumps {
  margin-bottom: 10px;
}

.first__prev {
  font-family: var(--font_main);
  font-weight: 500;
  font-size: 12px;
  line-height: 170%;
  color: #a3a3a3;
  transition: all 0.2s ease-out;

  &::after {
    content: "/";
    margin: 0 6px;
  }

  &:hover {
    color: #4d5051;
  }
}

.first__current {
  font-family: var(--font_main);
  font-weight: 500;
  font-size: 12px;
  line-height: 170%;
  color: #4d5051;
}

.first__title.intro__title {
  margin-bottom: 20px;
  text-transform: none;
  text-align: left;
  color: #1f1e1d;

  @include lg {
    font-size: 56px;
  }

  @include md {
    font-size: 42px;
  }

  @include sm {
    font-size: 36px;
  }

  @include xxs {
    font-size: 28px;
  }
}

.first__text.intro__text {
  margin-bottom: 40px;

  @include md {
    margin-bottom: 20px;
  }

  @include sm {
    margin-bottom: 20px;
  }

  @include xxs {
    font-size: 14px;
  }

  p {
    margin-bottom: 10px;
  }
}

.first__img {
  img {
    display: block;
    width: 100%;
    height: auto;
  }
}

.first.our-partners__page {
  @include md {
    padding: 51px 0 85px;

    .first__text.intro__text {
      margin-bottom: 30px;
    }
  }

  @include sm {
    padding: 33px 0 70px;

    .first__text.intro__text {
      margin-bottom: 20px;
    }
  }

  @include xxs {
    padding: 20px 0 65px;
  }

  @include xxxs {
    padding: 30px 0 30px;
  }
}

.our-partners__page .first__img {
  width: 512px;

  @include md {
    margin-left: -56px;
  }

  @include r(991px) {
    display: none;
  }
}

